<template>
  <div class="box">
    <div class="mainBox">
      <!-- 轮播图 -->
      <el-carousel :interval="4000" type="card">
        <el-carousel-item v-for="item in banners" :key="item.id">
          <img :src="item.imageUrl" alt="" />
        </el-carousel-item>
      </el-carousel>

      <!-- 推荐歌单 -->
      <h4>推荐歌单</h4>
      <RecommendMusic :recommendResource="recommendResource" />

      <!-- 独家放送 -->
      <h4>独家放送</h4>
      <IndividuationMv />

      <!-- 最新音乐 -->
      <h4 class="newsongs">最新音乐</h4>
      <NewSong />
    </div>
  </div>
</template>


<script>
import { getPersonalized, banner } from "../../network/discover.js";
import RecommendMusic from "./RMusicList.vue";
import IndividuationMv from "./IndividuationMv.vue";
import NewSong from "./NewSong.vue";
export default {
  name: "DicoverMusic",
  components: {
    RecommendMusic,
    IndividuationMv,
    NewSong
  },
  data() {
    return {
      recommendResource: [],
      banners: []
    };
  },
  created() {
    // 获取轮播图
    banner().then(res => {
      this.banners = res.data.banners.slice(0, 6);
    });

    // 获取推荐歌单
    getPersonalized().then(res => {
      this.recommendResource = res.data.result;
    });
  },
  methods: {}
};
</script>


<style scoped>
.mainBox {
  margin: 0 auto;
}
img {
  width: 500px;
  height: 300px;
}
.newsongs {
  padding-bottom: 30px;
  border-bottom: 1px solid #cdcdcd;
}
h4 {
  margin: 0px;
  padding: 20px 0;
  border-bottom: 1px solid rgb(220, 220, 220);
}
</style>